.wait {
    max-width: 100%;
    background-position: 100% 50%;
    background-size: cover;
    background-repeat: no-repeat;
    margin: 50px auto;
    @include clr;
    &__back {
        opacity: 0.92;
        background: linear-gradient(#011627, #ff9f1c); }
    &__container {
        max-width: 725px;
        height: 404px;
        margin: 0 auto;
        position: relative;
        @include r(640) {
            height: 600px; } }
    &__title {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        font-size: 60px;
        font-family: "Rubik Medium", sans-serif;
        color: #ffffff;
        line-height: 1.0;
        width: 52%;
        float: left;
        @include r(640) {
            top: 0;
            transform: translateY(25%);
            position: static;
            width: 100%; } }
    &__table {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 48%;
        float: right;
        right: 0;
        @include r(640) {
            position: static;
            width: 100%;
            transform: translateY(50%); }
        table {
            border: 3px solid orange;
            td {
                font-size: 16px;
                font-family: "Martel";
                color: #ffffff;
                line-height: 0.979;
                text-align: left;
                padding: 5%; } } } }
