
/* Advanced Checkbox Hack */
body {
    -webkit-animation: bugfix infinite 1s;
    font-family: "Martel", sans-serif; }

@-webkit-keyframes bugfix {
    from {
        padding: 0; }

    to {
        padding: 0; } }

* {
    box-sizing: border-box; }

.header {
    margin: 50px auto;
    max-width: 940px;
    width: 100%;
    @include clr; }

.container {
    width: 100%;
    margin: 0 auto;
    padding: 0;
    text-align: center;
    position: relative; }

.content {
    font: 1em 'Open Sans', sans-serif;
    margin: .75em 0 0 0;
    transition: margin .3s ease-in-out; }

.content > p {
    margin: .25em 0 .75em 0;
    text-align: justify; }

input[type=checkbox] {
    position: absolute;
    top: -9999px;
    left: -9999px; }

label {
    position: absolute;
    left: 0;
    display: none;
    width: 2em;
    height: 2.25em;
    padding: .35em;
    font-size: 1.1em;
    color: #fff;
    transition: color .3s ease-in-out;
    cursor: pointer;
    user-select: none;
    margin: 0;
    background: rgba(0, 0, 0, 0.4); }

.logo {
    line-height: 1.0;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    &__link {
        text-decoration: none;
        font-size: 26px;
        font-family: "Martel", sans-serif; }
    &__part1 {
        color: #0e1d2b; }
    &__part2 {
        font-weight: bold;
        color: #ffa326; }
    &:hover {
        font-weight: bold; } }

.nav {
    line-height: 1.0;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    float: right;
    text-align: right;
    ul {
        float: right; }
    &__link {
        font-size: 16px;
        font-family: "Martel", sans-serif;
        text-decoration: none;
        color: #000000;
        border-bottom: 3px solid rgba(0,0,0,0);
        &:hover {
            font-weight: bold;
            border-bottom: 3px solid orange; } } }

li {
    float: left;
    text-align: center;
    margin-right: 15px;
    display: table; }

@media screen and (max-width: 44em) {
    $menu_width: 20em;

    html, body {
        margin: 0;
        overflow-x: hidden; }

    .header {
        margin: 0; }

    .container {
        margin: 0;
        padding: 0; }

    .content {
        margin: 2.5em .5em 0 .5em; }

    .logo {
        position: relative;
        top: 15px;
        right: 15px;
        transform: translateY(0);
        float: right; }

    nav[role="off-canvas"] {
        position: absolute;
        transform: translateY(0);
        top: 3em;
        left: -$menu_width;
        width: $menu_width;
        opacity: 0;
        padding: 0;
        ul > li {
                height: 100%;
                width: 100%;
                margin: 0;
                text-align: left; }
        ul > li:hover {
                box-shadow: inset 0 0 0 0.15em rgba(255, 255, 255, 0.45);
                border-bottom-width: 3px;
                border-bottom-color: none;
                background-color: orange;
                color: #ffffff; } }
    label {
        display: block; }

    label:after {
        position: absolute;
        top: 0;
        right: .25em;
        content: "\2261";
        font-size: 1.8em; }

    label:hover,
    input:checked ~ label {
        color: #000; }

    .nav {
        &__link {
            font-size: 26px;
            &:hover {
                border-bottom: none; } } }


    input:checked ~ .header > .container > nav[role="off-canvas"] {
        opacity: 1;
        left: 0;
 }        // box-shadow: -30em 0 0 30em rgba(0, 0, 0, 0.3)

    input:checked ~ .header > .container > nav[role="off-canvas"] ul > li {
        padding: .55em .55em .55em 1.5em; }

    input:checked ~ .header > .container > nav[role="off-canvas"] ul > li:hover {
        padding-left: 3.5em; }

    input:checked ~ .header > .container > nav[role="off-canvas"] ul > li:after {
        position: absolute;
        right: .25em;
        content: "\203A";
        font: bold 1.4em sans-serif;
        color: inherit; }

    input:checked ~ .content {
        margin-left: $menu_width + .5em;
        margin-right: -($menu_width + .5em); } }
