.contact {
    margin-top: 60px;
    &__body {
        background-color: #0d1e2d;
        @include clr; }
    &__container {
        max-width: 725px;
        margin: 0 auto;
        padding: 30px 0; } }

.form {
    width: 35.862068965517241379310344827586%;
    float: left;
    @include clr;
    @include r(640) {
        float: none;
        width: 100%; }
    &__title {
        font-size: 12.18px;
        font-family: "Martel";
        color: #ffffff;
        line-height: 1;
        text-align: left;
        @include r(640) {
            margin: 0 10px; } }
    &__input, &__button {
        display: block;
        line-height: 0.8;
        margin: 5px 0;
        input {
            width: 100%;
            height: 30px;
            @include r(640) {
                width: 95%;
                margin: 0 10px; } } }
    &__button {
        input {
            background-color: #ffa326;
            color: #ffffff;
            font-size: 16px;
            font-family: "Martel";
            font-weight: bold;
            line-height: 0.979;
            text-align: center; } }
    &__textarea {
        display: block;
        @include r(640) {
            width: 95%;
            margin: 0 10px; } } }

.local {
    float: right;
    width: 43.862068965517241379310344827586%;
    @include clr;
    @include r(640) {
        float: none;
        width: 95%;
        margin: 10px auto; }
    &__address {
        font-size: 18.52px;
        font-family: "Martel";
        color: #ffffff;
        line-height: 0.979;
        @include r(640) {
            margin: 10px auto; } }
    &__call {
        display: inline-block;
        font-size: 18.52px;
        font-family: "Martel";
        color: #ffffff;
        line-height: 0.979; }
    &__phone {
        display: inline-block;
        font-size: 46.83px;
        font-family: "Martel";
        color: #ffa326;
        font-weight: bold;
        line-height: 0.979;
        text-align: center; }
    img {
        @include r(640) {
            width: 95%; } } }
