html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

/* Advanced Checkbox Hack */
body {
  -webkit-animation: bugfix infinite 1s;
  font-family: "Martel", sans-serif;
}

@-webkit-keyframes bugfix {
  from {
    padding: 0;
  }
  to {
    padding: 0;
  }
}

* {
  box-sizing: border-box;
}

.header {
  margin: 50px auto;
  max-width: 940px;
  width: 100%;
}

.header:after {
  content: " ";
  display: table;
  clear: both;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0;
  text-align: center;
  position: relative;
}

.content {
  font: 1em 'Open Sans', sans-serif;
  margin: .75em 0 0 0;
  transition: margin .3s ease-in-out;
}

.content > p {
  margin: .25em 0 .75em 0;
  text-align: justify;
}

input[type=checkbox] {
  position: absolute;
  top: -9999px;
  left: -9999px;
}

label {
  position: absolute;
  left: 0;
  display: none;
  width: 2em;
  height: 2.25em;
  padding: .35em;
  font-size: 1.1em;
  color: #fff;
  transition: color .3s ease-in-out;
  cursor: pointer;
  user-select: none;
  margin: 0;
  background: rgba(0, 0, 0, 0.4);
}

.logo {
  line-height: 1.0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.logo__link {
  text-decoration: none;
  font-size: 26px;
  font-family: "Martel", sans-serif;
}

.logo__part1 {
  color: #0e1d2b;
}

.logo__part2 {
  font-weight: bold;
  color: #ffa326;
}

.logo:hover {
  font-weight: bold;
}

.nav {
  line-height: 1.0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  float: right;
  text-align: right;
}

.nav ul {
  float: right;
}

.nav__link {
  font-size: 16px;
  font-family: "Martel", sans-serif;
  text-decoration: none;
  color: #000000;
  border-bottom: 3px solid transparent;
}

.nav__link:hover {
  font-weight: bold;
  border-bottom: 3px solid orange;
}

li {
  float: left;
  text-align: center;
  margin-right: 15px;
  display: table;
}

@media screen and (max-width: 44em) {
  html, body {
    margin: 0;
    overflow-x: hidden;
  }
  .header {
    margin: 0;
  }
  .container {
    margin: 0;
    padding: 0;
  }
  .content {
    margin: 2.5em .5em 0 .5em;
  }
  .logo {
    position: relative;
    top: 15px;
    right: 15px;
    transform: translateY(0);
    float: right;
  }
  nav[role="off-canvas"] {
    position: absolute;
    transform: translateY(0);
    top: 3em;
    left: -20em;
    width: 20em;
    opacity: 0;
    padding: 0;
  }
  nav[role="off-canvas"] ul > li {
    height: 100%;
    width: 100%;
    margin: 0;
    text-align: left;
  }
  nav[role="off-canvas"] ul > li:hover {
    box-shadow: inset 0 0 0 0.15em rgba(255, 255, 255, 0.45);
    border-bottom-width: 3px;
    border-bottom-color: none;
    background-color: orange;
    color: #ffffff;
  }
  label {
    display: block;
  }
  label:after {
    position: absolute;
    top: 0;
    right: .25em;
    content: "\2261";
    font-size: 1.8em;
  }
  label:hover,
  input:checked ~ label {
    color: #000;
  }
  .nav__link {
    font-size: 26px;
  }
  .nav__link:hover {
    border-bottom: none;
  }
  input:checked ~ .header > .container > nav[role="off-canvas"] {
    opacity: 1;
    left: 0;
  }
  input:checked ~ .header > .container > nav[role="off-canvas"] ul > li {
    padding: .55em .55em .55em 1.5em;
  }
  input:checked ~ .header > .container > nav[role="off-canvas"] ul > li:hover {
    padding-left: 3.5em;
  }
  input:checked ~ .header > .container > nav[role="off-canvas"] ul > li:after {
    position: absolute;
    right: .25em;
    content: "\203A";
    font: bold 1.4em sans-serif;
    color: inherit;
  }
  input:checked ~ .content {
    margin-left: 20.5em;
    margin-right: -20.5em;
  }
}

